import React from "react";
import { FaCookieBite, FaInstagram } from "react-icons/fa";
import { Link, useStaticQuery, graphql } from "gatsby";

const Footer = () => {
  const data = useStaticQuery(graphql`
    query {
      allSanitySettings {
        edges {
          node {
            instagram
          }
        }
      }
    }
  `);

  return (
    <div className="footer d-flex justify-content-between px-1 align-items-center">
      <p className="text-start m-0">
        <Link className="text-reset" to="/privacy">
          Privacy and <FaCookieBite />
        </Link>
      </p>
      {data.allSanitySettings?.edges.length > 0 && data.allSanitySettings?.edges[0].node?.instagram && (
        <p className="text-end m-0">
          <a className="text-reset" href={`${data.allSanitySettings?.edges[0].node?.instagram}`}>
            <FaInstagram className="m-1 insta" size={24} />
          </a>
        </p>
      )}
    </div>
  );
};
export default Footer;
