import { graphql, useStaticQuery } from "gatsby";

export const useSiteMetadata = () => {
  const data = useStaticQuery(graphql`
    query {
      allSanitySettings {
        edges {
          node {
            seoTitle
            seoUrl
            seoImage {
              asset {
                url
              }
            }
            seoDescription
            seoKeywords
          }
        }
      }
    }
  `);

  return data?.allSanitySettings?.edges?.length > 0 ? data?.allSanitySettings?.edges[0].node : null;
};
