import * as React from "react";
import { useSiteMetadata } from "../../hooks/use-site-metadata";

const Seo = ({ title, description, keywords, pathname, image, children }) => {
  const {
    seoTitle: defaultTitle,
    seoDescription: defaultDescription,
    seoImage: { asset: { url: defaultImage }},
    seoUrl: defaultUrl,
    seoKeywords: defaultKeywords,
  } = useSiteMetadata();

  const seo = {
    title: title || defaultTitle,
    description: description || defaultDescription,
    image: image || defaultImage,
    keywords: keywords || defaultKeywords,
    url: `${defaultUrl}${pathname || ``}`,
  };

  return (
    <>
      <title>{seo.title}</title>
      <meta name="description" content={seo.description} />
      <meta name="image" content={seo.image} />

      <meta property="og:title" content={seo.title} />
      <meta property="og:type" content="website" />
      <meta property="og:image" content={seo.image} />
      <meta property="og:url" content={seo.url} />
      <meta name="twitter:card" content="summary" />

      <meta property="og:description" content={seo.description} />
      <meta property="og:site_name" content={seo.title} />
      <meta name="twitter:image:alt" content={`${seo.title} logo`} />

      {children}
    </>
  );
};

export default Seo;
